body {
  color: #FFF;
  background-color: #01082d;
  background: #e9275b;
  /* Old browsers */
  /* FF3.6-15 */
  /* Chrome10-25,Safari5.1-6 */
  background: -webkit-gradient(linear, left top, left bottom, from(#e9275b), to(#ffff66));
  background: linear-gradient(to bottom, #e9275b 0%, #ffff66 100%);
  /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#e9275b', endColorstr='#ffff66',GradientType=0 );
  /* IE6-9 */
  font-size: 14px;
  position: relative;
  font-family: "Montserrat", sans-serif;
  margin: 0;
  width: 100%;
  background-attachment: fixed; }

.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0; }

.page-content {
  min-height: calc(100vh - 123px);
  background: #01082d;
  margin-top: -2px;
  padding-top: 100px;
  z-index: 100; }
  .page-content .content-wrapper {
    max-width: 1000px;
    margin: 0 auto; }
